<script>

import Vue from 'vue'
import axios from 'axios';
import VueNotifications from 'vue-notifications'
import miniToastr from 'mini-toastr'// https://github.com/se-panfilov/mini-toastr
import {restapiPrefix,wprestapiPrefix} from './../../variables.js'
import router from '../../../router';
import Loading from 'vue-loading-overlay';
import 'vue-loading-overlay/dist/vue-loading.css';
import Multiselect from 'vue-multiselect';
import { VueEditor } from "vue2-editor";


const toastTypes = {
  success: 'success',
  error: 'error',
  info: 'info',
  warn: 'warn'
}

const options = {
  success: toast,
  error: toast,
  info: toast,
  warn: toast
}

miniToastr.init({types: toastTypes})

function toast ({title, message, type, timeout, cb}) {
  return miniToastr[type](message, title, timeout, cb)
}

export default {
  name: 'compose',
  components: {
    Multiselect,
    VueEditor
  },
  data () {
    return {
      to_emails: [],
      all_emails:[],
      showGroups:false,
      showIndividual:false,
      allGroups:[],
      allCaregivers:[],
      value: [],
      subject:'',
      message:''
    }
  },
  beforeCreate: function () {
    if(localStorage.user_id == undefined || localStorage.user_id == "0")
    {        
      router.push({ name: "Login"})
    }  
  },  
  created() {
    this.userId = localStorage.user_id;
    this.getGroups();
    this.getAllCaregivers();
  },
   methods: {
    getAllCaregivers() {
      let asksUri = restapiPrefix+'/caregivers/getCaregiversByUserID/'+this.userId;
      this.axios.get(asksUri).then(response => {
        this.to_emails = [];
        this.allCaregivers = response.data;
        for(let i = 0; i < response.data.length; i++) {
          let to = response.data[i].email_address;
          this.all_emails.push(to);
        }
        console.log(this.to_emails);
      });
    },
    getGroups(){
      let asksUri = restapiPrefix+'/caregiverGroups/getGroupsByUserID/'+this.userId;
      this.axios.get(asksUri).then(response => {
        //console.log(response.data);
        this.allGroups = response.data;
      });
    },
    selectGroup(group_id)
    {
        this.to_emails= [];
        let emails = [];
        for(let i = 0; i < this.allCaregivers.length; i++) {
          if(this.allCaregivers[i].caregiver_group == group_id)
          {
            this.to_emails.push(this.allCaregivers[i].email_address);
          }
        }
    },
    selectIndividual(){
      this.showIndividual = true;
    },
    onSelectIndividual(email){
      this.to_emails=[];
      if(email.length > 0){
        for(var i=0;i<email.length;i++){
          //alert(email[i].email_address);
          this.to_emails.push(email[i].email_address)
        }
      }
    },
    addTag (newTag) {
      const tag = {
        name: newTag,
        code: newTag.substring(0, 2) + Math.floor((Math.random() * 10000000))
      }
      this.options.push(tag)
      this.value.push(tag)
    },
    sendMessage()
    {
      if(this.to_emails.length == 0 || this.subject == '' || this.message == '')
      {
        miniToastr.error("Please enter all fields", 'Empty Fields Error');
        return false;
      }

      var data = {
            'user_id':this.userId,
            'to':this.to_emails,
            'subject':this.subject,
            'message':this.message
      }
        axios.post(wprestapiPrefix+'/wp-json/custom-plugin/fn_send_message_MVEN',data).then(response => {
        //axios.post('http://whatisquire.com/wp-json/custom-plugin/fn_get_progress_MVEN',data).then(response => {
          console.log('response==>'+response.data['status']);
          if(response.data['status'] == 'success')
          {
            //this.progress = response.data['data'];
            //this.getCaregiverListByUserId(this.userId);
            //console.log('this.progress==>'+this.progress);
            this.to_emails = [];
            this.subject='';
            this.message='';
            this.showGroups=false,
            this.showIndividual=false,
            miniToastr.success('Message sent successfully', 'Success');
          }
          else{
            console.log('something went wrong!');
            miniToastr.error('something went wrong!', 'Error');
          }
      });

    }
   }
}
</script>
